.services {
  padding-top: 0rem;
}

.services-image-background {
  background: url(https://i.ibb.co/6Dc0Hhp/services-left-Side-image.jpg) no-repeat left;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.services-title-col-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #333E51;
  background-image: linear-gradient(315deg, #333E51 0%, #333E51 74%);
  padding: 3rem 0;
}

.services-h1-title {
  font-weight: 600;
  color: rgb(255, 255, 255);
  text-align: center;
  font-size: 2.5rem;
  letter-spacing: 0.3rem;
  margin: 0.5rem 0;
  font-family: 'Domine', serif;
}

/*.services-h1-title-span {
  margin-bottom: 1rem;
  background-color: #333E51;
  padding: 0.2rem 5rem;
  border-bottom-left-radius: 99999px;
  border-bottom-right-radius: 99999px;
}*/

.services-col-rightSide-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #333E51;
  background-image: linear-gradient(315deg, #333E51 0%, #333E51 74%);
}

.services-col-leftSide-container {
  padding: 0 !important;
}

.services-service-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row !important;
}

.services-card {
  padding: 1rem 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cardTitle {
  color: white;
}

.services-service-box-button {
  padding: 3rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.services-services-button-us {
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: #333e51 !important;
  padding: 0.4rem 2.3rem !important;
}

@media screen and (max-width: 1200px) {
  .services-h1-title {
    font-size: 2.1rem;
  }

  .services-card {
    padding: 3rem 1.5rem;
    flex: 0 0 0%;
  }

  .services-service-box-button {
    padding: 3rem 0;
    flex: 0 0 0%;
  }

  .dev-icon {
    font-size: 2.3rem;
  }

  .cardTitle {
    font-size: 1.2rem !important;
  }

  .services-card:hover {
    transform: scale(1);

  }

}

*::-webkit-media-controls-panel {
  display: none !important;
  -webkit-appearance: none;
}

/* Old shadow dom for play button */

*::-webkit-media-controls-play-button {
  display: none !important;
  -webkit-appearance: none;
}

/* New shadow dom for play button */

/* This one works! */

