@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

.services-page-plus {
    background-color: rgb(238, 238, 238);
    font-family: 'Poppins', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 4rem;
}

.services-row {
    padding: 1rem 0rem;
}

.cols-title {
    text-align: center;
    color: rgb(255, 255, 255);
    font-family: 'Domine', serif;
    letter-spacing: 0.35rem;
    padding-bottom: 1rem;
    font-size: 2.5rem;
}

.cols-para {
    color: rgb(255, 255, 255);
    font-size: 1rem;
    padding: 0 6rem;
}

.cols-section-num {
    color: rgb(255, 255, 255);
    font-size: 25rem;
    position: absolute;
    opacity: 0.1;
    z-index: 0;
}

.imageBackground-div2 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25rem;
    margin-right: 30rem;
    background: linear-gradient(to top, #333E51 10%, #ffffff 100%);
    width: 7rem;
    height: 7rem;
    border-radius: 100%;
    position: absolute;
    opacity: 0.7;
    z-index: 1;
}

.imageBackground-div2-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25rem;
    margin-left: 25rem;
    background: linear-gradient(to top, #333E51 10%, #ffffff 100%);
    width: 7rem;
    height: 7rem;
    border-radius: 100%;
    position: absolute;
    opacity: 0.7;
    z-index: 1;
}

.imageBackground-div2-title {
    color: rgb(210, 172, 44);
    font-size: 4.5rem;
    position: absolute;
    opacity: 0.9;
    z-index: 0;
}

.secondRow-cols {
    display: flex;
    justify-content: center;
    align-items: center;
}

.secondRow-cols,
.thirdRow-cols,
.fourthRow-cols,
.firstRow-cols {
    background: #333E51;
    width: 100%;
    padding: 5rem;
}

@media screen and (max-width: 1200px) {
    .services-page-plus {
        margin-top: 0rem;
    }

    .cols-title {
        font-size: 1.6rem;
        letter-spacing: 0rem;
        padding-bottom: 1rem;
    }

    .cols-para {
        font-size: 0.6rem;
        padding: 0 0.5rem;
    }

    .cols-text-content-div {
        padding: 0 0rem;
        z-index: 1;
    }

    .web-app-deve-section {
        margin-bottom: 0rem;
    }

    .cols-section-num {
        font-size: 5rem;
        opacity: 0.2;
    }

    .imageBackground-div2 {
        margin-top: 11rem;
        left: 1rem;
        width: 3rem;
        height: 3rem;
        opacity: 0.7;
        display: none;
    }

    .imageBackground-div2-1 {
        margin-top: 11rem;
        right: 0rem;
        width: 4rem;
        height: 4rem;
        opacity: 0.7;
        display: none;
    }

    .imageBackground-div2-title {
        font-size: 2rem;
        opacity: 0.7;
    }

    .services-page-plus-image {
        width: 150px;
        padding: 0rem;
    }

    .cols-text-content-div {
        padding: 1.5rem 0rem;
    }

    .services-row {
        padding: 0rem;
    }

    .services-card2 {
        padding: 0rem;
    }

    .secondRow-cols,
    .thirdRow-cols,
    .fourthRow-cols,
    .firstRow-cols {
        background: #333E51;
        width: 100%;
        padding: 0rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .services-row {
        padding: 0rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        background: #333E51;
    }

    .services-page-fourthRow {
        margin-bottom: 3rem;
    }
}