@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap');

.about {
  font-family: "Montserrat", sans-serif;
  background-color: transparent;
}

.about-us-fp-mainRow {
  background: url(https://i.ibb.co/0c3yHNK/About-Us-FPBackground.jpg) no-repeat center;
  background-size: contain;
  background-attachment: fixed;
  background-position: left;
  width: 100%;
  min-height: 41rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.about-us-fp-firstPart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  background-color: rgba(210, 171, 44, 0.91);
  border-radius: 100%;
  width: 100%;
  padding: 5.5rem 0rem;
}

.about-us-fp-firstPart-content-div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
  flex-direction: column;
  padding-left: 2rem;
}

.about-us-fp-h1-title {
  font-weight: 600;
  font-family: 'Domine', serif;
  letter-spacing: 0.3rem;
  font-size: 3.5rem;
  padding: 1rem 0rem;
  text-align: left;
}

.about-us-fp-para-text {
  padding: 1rem 0rem;
}

.about-us-fp-button {
  margin: 2rem 0 !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: #333e51e7 !important;
  padding: 0.4rem 2.3rem !important;
}

.about-us-fp-button-editedCss {
  margin: 2rem 0 !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: rgba(210, 171, 44, 1) !important;
  padding: 0.4rem 2.3rem !important;
}


.about-us-fp-our-capabilities {
  margin: 2rem 0;
  padding: 2rem 0;
  background: linear-gradient(to right, #333e51cb 50%, #ffffff 50%);
}

.about-us-fp-our-capabilities-leftSide {
  background: #333e51;
  color: white;
  width: 100%;
  height: 100%;
  padding: 5rem;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.about-us-fp-h5-our-capabilities {
  padding-bottom: 1.5rem;
  letter-spacing: 0.3rem;
  font-family: 'Domine', serif;
  font-size: 2.5rem;
}

.about-us-fp-h2-text {
  max-width: 20rem;
  font-size: 1.3rem !important;
  padding-left: 1rem;
  padding-top: 2.5rem;
  line-height: 3.5rem;
}

.about-us-fp-col-rightSide {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.about-us-fp-row-our-capabilites-items {
  display: flex;
  justify-content: center;
  align-items: center;
}


.about-us-fp-our-capabilities-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #333e51;
  padding: 1rem 2rem;
  margin: 1.5rem 1rem;
  color: rgba(210, 171, 44, 1);
  font-size: 1.2rem;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.checked-materialUi-icon {
  color: rgb(255, 255, 255);
  padding-left: 1rem;
  font-size: 3rem !important;
}

.about-us-fp-rightSide-para {
  max-width: 40rem;
}

.howWeWork-row-container {
  margin: 2rem 0;
}

.howWeWork-leftSide-backgroundImage-overlay {
  background: url(https://i.ibb.co/4m9FSnB/about-us-fp-how-We-Workleft-Side.jpg) no-repeat center;
  background-size: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
}

.howWeWork-leftSide-backgroundColor-overlay {
  background-color: rgba(51, 62, 81, 0.658);
  z-index: 2;
  width: 100%;
  height: 100%;
  padding: 0;
}

.howWeWork-leftSide-content {
  color: white;
  padding: 3rem;
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.howWeWork-rightSide-backgroundImage-overlay {
  background: url(https://i.ibb.co/9v4sy46/how-We-Work-right-Side.jpg) no-repeat center;
  background-size: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
}

.howWeWork-rightSide-backgroundColor-overlay {
  background-color: rgba(210, 171, 44, 0.71);
  z-index: 2;
  width: 100%;
  height: 100%;
  padding: 0;
}

.partner-span {
  color: rgba(51, 62, 81, 1);
}

.howWeWork-rightSide-content {
  color: white;
  padding: 3rem;
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.howWeWork-leftSide-col {
  padding: 0 !important;
}

.howWeWork-rightSide-col {
  padding: 0 !important;
}

.about-us-fp-h4-howWeWork {
  max-width: 25rem;
  font-family: 'Domine', serif;
  letter-spacing: 0.1rem;
}

@media screen and (max-width: 1200px) {
  .about-us-fp-h1-title {
    letter-spacing: 0.2rem;
    font-size: 2.1rem;
    padding: 1rem;
  }

  .about-us-fp-para-text {
    padding: 0rem;
  }

  .about-us-fp-mainRow {
    background: url(https://i.ibb.co/0c3yHNK/About-Us-FPBackground.jpg) no-repeat center;
    background-size: cover;
    background-position: center;
    margin: 0 !important;
    background-attachment: fixed;
    min-height: 100%;
  }

  .about-us-fp-firstPart {
    flex-direction: column;
    background-color: rgba(210, 171, 44, 0.75);
    border-radius: 0%;
    width: 100%;
    padding: 2rem 0rem;
  }

  .about-us-fp-h5-our-capabilities {
    padding-bottom: 0rem;
    letter-spacing: 0rem;
    font-family: 'Domine', serif;
    font-size: 2rem;
  }

  .about-us-fp-our-capabilities-leftSide {
    width: 100%;
    height: 100%;
    padding: 2.5rem;
    box-shadow: none;
  }

  .about-us-fp-our-capabilities {
    padding: 0;
  }

  .about-us-fp-h2-text {
    max-width: 20rem;
    font-size: 0.9rem !important;
    padding-left: 0rem;
    padding-top: 2.5rem;
    line-height: 1.5rem;
  }

  .about-us-fp-our-capabilities-item {
    padding: 0rem 1.2rem;
    margin: 0;
    color: rgba(210, 171, 44, 1);
    font-size: 0.8rem;
    box-shadow: none;
    border: 2px dashed white;
    margin: 0.5rem 0.5rem;
    border-radius: 99999px;
  }

  .about-us-fp-h4-howWeWork {
    max-width: 20rem;
    text-align: center;
    padding-top: 1rem;
    font-size: 1rem;
  }
}