@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Courier+Prime&family=IBM+Plex+Sans:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap');

.join-us {
    font-family: "Poppins", sans-serif;
    color: rgb(0, 0, 0);
}

.join-us-mainRow {
    background: linear-gradient(to top, #bb9723 50%, #ffffff 50%);
    padding: 5rem 3rem;
}

.join-us-page-title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.join-our-team-h1-title {
    font-size: 3rem;
    padding-bottom: 3rem;
    padding-top: 1rem;
    font-family: 'Domine', serif;
}

.join-our-team-span,
.itpeaks-email-span {
    color: #D2AC2C;
}

.job-on-board-card {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #333e51;
    color: white;
    margin: 1rem 0;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 3rem;
}

.job-on-board-card-h1-title {
    padding: 1rem;
}

.job-on-board-card-para-content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.job-vacancies-card {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    color: white;

}

.job-vacancies-leftSide {
    padding: 0rem;
    background-color: #333e51;
    color: white;

}

.job-vacancies-leftSide-images {
    width: 100%;
    height: 100%;
    padding: 1rem 0;
}

.job-vacancies-rightSide-col {
    background-color: #e4bd3c;
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0rem;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.job-vacancies-rightSide {
    padding: 1rem 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.button-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.apply-button {
    color: rgb(255, 255, 255) !important;
    border-radius: 999999px !important;
    background-color: #D2AC2C !important;
    padding: 0.4rem 2.3rem !important;
    text-decoration: none !important;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    margin-left: 1rem;
}

.job-vacancies-leftSide-col {
    display: flex;
    justify-content: center;
    align-items: center;
}

.join-us-main-secondRow2 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem;
}

.secondRow2-cols-1-input {
    display: flex !important;
}

.secondRow2-1-cols-button,
.secondRow2-1-cols-textarea {
    padding: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.join-us-phone-number-input {
    color: white !important;
}

.inputfile {
    cursor: pointer;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile+label {
    padding: 0rem;
    color: white;
    background-color: transparent;
}

.inputfile+label {
    cursor: pointer;
}


.file-input-div {
    padding: 2rem 0rem;
    margin-top: 2rem;
    border-radius: 999999px;
    background-color: #333E51;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}


.join-us-leftSide-inner-col2 {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(211, 176, 63);
    font-family: 'Domine', serif;
    padding: 1rem 0;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    text-align: center;
}

.join-us-secondRow2-col {
    background-color: #333E51;
    display: flex;
    justify-content: center;
    align-items: center;
}

.join-us-secondRow2 {
    padding: 2rem;
}

.join-us-secondRow2 {
    width: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.join-us-secondRow2-title {
    font-size: 2rem;
    color: rgb(255, 255, 255);
    font-family: 'Domine', serif;
    padding-bottom: 3rem;
    letter-spacing: 0.1rem;
}

.join-us-secondRow-form {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 1200px) {
    .join-us-mainRow {
        background: linear-gradient(to top, #bb9723 50%, #ffffff 50%);
        padding: 0rem;
    }

    .join-our-team-h1-title {
        font-size: 2.1rem;
        padding-bottom: 1rem;
        padding-top: 1rem;
        font-family: 'Domine', serif;
    }

    .job-on-board-card {
        color: white;
        margin: 0rem;
        box-shadow: none;
        padding: 2rem;
    }

    .job-on-board-card-h1-title {
        font-size: 1.5rem;
        text-align: center;
    }

    .jobOnBoard-subtitle-text {
        font-size: 0.8rem;
        max-width: 20rem;
    }

    .job-vacancies-rightSide {
        padding: 0rem;
    }

    .thirdRow4-cols2-h4 {
        font-size: 1.1rem;
        padding: 1rem 0;
    }

    .thirdRow4-cols2-h6 {
        font-size: 0.8rem;
    }

    .job-vacacies-card-para-text {
        padding-top: 1rem;
        font-size: 0.8rem;
        max-width: 14rem;
    }

    .job-vacancies-buttons-div {
        transform: scale(0.75);
        padding-bottom: 1rem;
    }

    .job-vacancies-leftSide-images {
        width: 200%;
        height: 100%;
        padding: 1rem 0;
    }

    .join-us-main-secondRow2 {
        margin: 3rem 0 !important;
    }

    .join-us-secondRow2-title {
        font-size: 1.5rem;
        color: rgb(255, 255, 255);
        font-family: 'Domine', serif;
        padding-bottom: 1rem;
        letter-spacing: 0.1rem;
    }
}