@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url('https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap');

.about-mini {
    background-color: #ffffff;
    background: url(https://i.ibb.co/7n7GPvJ/about-Us-Mini-background.jpg) no-repeat center;
    background-size: cover;
    color: rgba(0, 0, 0, 0.767);
    padding-bottom: 5rem;
    margin-top: 0rem;
    position: relative;
    font-family: "Montserrat", sans-serif !important;
    padding: 2rem 0rem;
}

.about-us-mini-heading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 2rem 3rem;
    margin-bottom: 3rem;
    flex-direction: column;
}


.about-us-mini-heading-title {
    font-family: 'Domine', serif;
    letter-spacing: 0.3rem;
    font-size: 3rem;
}

.about-us-span-us {
    color: rgb(210, 172, 44);
}

.about-us-mini-heading-para {
    padding: 0 5rem;
    letter-spacing: 0.08rem;
}

.about-us-mini-content-row {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-us-mini-our-capabilities-col {
    color: white;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    padding: 9rem 3rem;
    background-color: rgb(210, 172, 44);
    text-align: center;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    margin-top: 3rem;
}

.about-us-mini-How-we-work-col {
    color: rgb(255, 255, 255);
    text-align: center;
    border-radius: 100%;
    padding: 8.5rem 3rem;
    background-color: #333e51;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin-top: -3rem;
    transform: scale(1.2);
}

.about-us-mini-leadership-col {
    color: white;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    padding: 9rem 3rem;
    background-color: rgba(210, 171, 44, 0.658);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    text-align: center;
    margin-top: 3rem;
}

.about-us-mini-button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 3rem 0;
}

.tele-link-nav {
    color: white !important;
    text-decoration: none !important;
}

@media screen and (max-width: 1200px) {
    .about-us-mini-heading-container {
        padding: 0;
        margin-bottom: 0;
    }

    .about-us-mini-heading-title {
        font-size: 2.1rem;
        padding-bottom: 1rem;
    }

    .about-us-mini-heading-para {
        padding: 0;
        letter-spacing: 0.08rem;
        max-width: 20rem;
    }

    .about-us-mini-our-capabilities-col {
        color: white;
        width: 100%;
        height: 100%;
        border-radius: 0;
        padding: 2rem 4rem;
        background-color: rgb(210, 172, 44);
        text-align: center;
        margin-top: 0rem;
    }

    .about-us-mini-How-we-work-col {
        border-bottom: 10px dashed rgb(210, 172, 44);
        border-top: 10px dashed rgb(210, 172, 44);
        color: rgb(255, 255, 255);
        text-align: center;
        border-radius: 0;
        padding: 2rem 3rem;
        background-color: #333e51;
        margin-top: 0rem;
        transform: scale(1);
        box-shadow: none;
    }

    .about-us-mini-leadership-col {
        color: white;
        width: 100%;
        height: 100%;
        border-radius: 0;
        padding: 2rem 3rem;
        background-color: rgba(210, 171, 44);
        text-align: center;
        margin-top: 0rem;
    }

    .about-us-mini-our-capabilities-h3-title,
    .about-us-mini-howWeWork-h3-title,
    .about-us-mini-our-capabilities-h3-title {
        font-size: 1.1rem;
    }

    .about-us-mini-our-capabilities-para-text {
        font-size: 0.7rem;
    }
}