.popup-para-div {
    color: black;
    padding: 2rem 1.5rem;
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.popup-h1 {
    text-align: center;
    color: #333E51;
    margin-bottom: 1rem;
}

.popup-h4 {
    text-align: center;
    color: #D2AC2C;
    margin-bottom: 3rem;
}

.learn-more-button {
    color: rgb(255, 255, 255) !important;
    border-radius: 999999px !important;
    background-color: #D2AC2C !important;
    padding: 0.4rem 2.3rem !important;
    text-decoration: none !important;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    border: none !important;
}

.learn-more-button:hover {
    opacity: 0.8;
}


.popup-close-button {
    margin-top: 2rem;
    color: rgb(255, 255, 255) !important;
    border-radius: 999999px !important;
    background-color: #333e51e7 !important;
    text-decoration: none !important;
    border-radius: 99999px;
    text-align: center !important;
    padding: 0.5rem 3rem;
}

.popup-close-button:hover {
    opacity: 0.8;

}

@media screen and (max-width: 1200px) {}